import { AdminDashboard2Component } from './../admin/admin-dashboard2/admin-dashboard2.component';
import { AdminDashboard1Component } from './../admin/admin-dashboard1/admin-dashboard1.component';



import { MyTimesheetComponent } from './../starter/MyTimesheet/mytimesheet.component';
import { EditTimesheetComponent } from './../starter/EditTimesheet/EditTimesheet.component';
import { NotificationsComponent } from './../starter/Notifications/Notifications.component';
import { TimeOffSubmissionComponent } from './../starter/TimeOffSubmission/TimeOffSubmission.component';
import { TimesheetApprovalsComponent } from './../starter/TimesheetApprovals/TimesheetApprovals.component';
import { TimeOffApprovalsComponent } from './../starter/TimeOffApprovals/TimeOffApprovals.component';
import { TimesheetHistoryComponent } from './../starter/TimesheetHistory/TimesheetHistory.component';
import { TeamAllocationsComponent }    from './../starter/TeamAllocations/team-allocations.component';

import { StarterComponent } from './../starter/starter.component';
import { AdminComponent } from './../admin/admin.component';
import { Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { SettingsComponent } from '../starter/settings/settings.component';
import { KanbanComponent } from '../starter/kanban/kanban.component';

const routes: Routes = [
  {
    path:'',
    component:MyTimesheetComponent
  },
  {
    path:'Mytimesheet',
    component:MyTimesheetComponent
  },
  {
    path:'EditTimesheet',
    component:EditTimesheetComponent
  },
  {
    path:'Notifications',
    component:NotificationsComponent
  }
  ,
  {
    path:'TimeOffSubmission',
    component:TimeOffSubmissionComponent
  }
  ,
  {
    path:'TimesheetApprovals',
    component:TimesheetApprovalsComponent
  }
  ,
  {
    path:'TimeOffApprovals',
    component:TimeOffApprovalsComponent
  }
  ,
  {
    path:'TimesheetHistory',
    component:TimesheetHistoryComponent
  },
  {
    path:'TeamAllocations',
    component:TeamAllocationsComponent
  },
  {
    path:'Settings',
    component:SettingsComponent
  },
  {
    path:'Kanban',
    component:KanbanComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



